/* Reset */

html, body, div {
    border: 0;
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
    width: 100%;
}

body {
    display: flex;
    flex-direction: column;
}

/* Box-sizing border-box */

*, *:before, *:after {
    box-sizing: border-box;
}

/* Set up a default font and some padding to provide breathing room */

body {
    font-family: Roboto, "Helvetica Neue", sans-serif;
    font-size: 16px;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

input {
    width: 250px;
}

hr {
    border-top: none;
    border-bottom: 1px solid #106cc8;
}

.recipe-box {
    display: inline-table;
    width: 400px;
    margin: 30px 30px 0 -7px;
    padding: 20px;
    border: 1px solid #106cc8;
}

.recipe-box input:first-child {
    width: 100%;
    margin: 11px 0;
}

@media only screen and (max-width: 600px) {
    .recipe-box {
        width: 100%;
    }
}

.recipe-box textarea {
    min-height: 300px;
    min-width: 400px;
    max-width: 500px;
}

.recipe-box ul {
    padding: 0 0 0 20px;
}

.empty-box {
    height: 200px;
    width: 100%;
    padding: 1.5em 0 1em 0;
    font-size: 3em;
    color: #ccc;
    border: 3px dashed;
    text-align: center;
    margin: 15px 0;
}

p {
    font-weight: 400;
    letter-spacing: 0.01em;
    line-height: 20px;
    margin-bottom: 1em;
    margin-top: 1em;
}

ul {
    margin: 10px 0 0 0;
    padding: 0 0 0 10px;
}

li {
    font-weight: 400;
    margin-top: 4px;
}

input, textarea {
    border: 1px solid #106cc8;
    font-size: 14px;
    height: 40px;
    outline: none;
    padding: 8px;
}

button {
    background-color: #106cc8;
    border-style: none;
    color: white;
    cursor: pointer;
    display: inline-block;
    font-size: 14px;
    font-weight: bold;
    height: 40px;
    padding: 8px 18px;
    text-decoration: none;
}

button:hover {
    background-color: #28739e;
}

button.small {
    font-size: 12px;
    height: 30px;
}

img.responsive-img {
    max-width: 100%;
    height: auto
}

.red {
    color: #e80000;
}

.right {
    float: right;
}

.center {
    text-align: center;
}

.hide {
    display: none;
}

/* ---- */

.App-header {
    background-color: #106cc8;
    padding: 0 17px;
    font-size: 12px;
    color: white;
}

h1 {
    display: inline;
    font-size: 20px;
    font-weight: normal;
    letter-spacing: 0.1px;
    line-height: 48px;
}

.more {
    background: url("./more.svg");
    float: right;
    height: 24px;
    margin-top: 12px;
    width: 24px;
}

nav {
    border-color: #e1e1e1;
    border-style: solid;
    border-width: 0 0 1px;
    display: block;
    height: 48px;
    padding: 0 16px;
}

nav a {
    color: #106cc8;
    font-size: 14px;
    font-weight: 500;
    line-height: 48px;
    margin-right: 20px;
    text-decoration: none;
    text-transform: uppercase;
    vertical-align: middle;
    cursor: pointer;
}

nav a.router-link-active {
    font-weight: bold;
}

div#home, div#about {
    padding: 0 15px;
}

div#home h1 {
    font-size: 2em;
    display: block;
    font-weight: bold;
    margin: 20px 0;
}

div#about h2 {
    font-size: 20px;
    font-weight: 500;
    letter-spacing: .005em;
    margin-bottom: 0;
    margin-top: .83em;
}
